* {
  -moz-user-select: none;
   -khtml-user-select: none;
   -webkit-user-select: none;
   -ms-user-select: none;
   user-select: none;
}

html, body {
  display: block;
  height: -webkit-fill-available;
}

body {
  font-family: 'Inconsolata-Light';
  margin: 0px;
  padding: 0px;
  background-color: beige;
  display: block;
  width: -webkit-fill-available;
  height: -webkit-fill-available;
}


@keyframes loadingOut {
  0% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 0; 
  }
}

#root {
  height: 100%;
}

#loading {
  z-index: 3;
  display: block;
  width: 100vw;
  height: 100vh;
  background: black;
  position: absolute;
  animation: 3s loadingOut;

}

#loading .inner {
  position: inherit;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

#loading .inner p {
  color: white;
  font-size: 2em;
  font-family: 'Inconsolata-Black';
  margin-bottom: 0;
}

#loading .inner span {
  color: #ffffff59;
  font-size: .8em;
  font-family: 'Inconsolata-Light';
}

@font-face {
  font-family: 'Inconsolata-Light';
  src: url('fonts/Inconsolata_Condensed-Light.ttf') format('truetype');
}

@font-face {
  font-family: 'Inconsolata-Bold';
  src: url('fonts/Inconsolata_Condensed-Bold.ttf') format('truetype');
}

@font-face {
  font-family: 'Inconsolata-Black';
  src: url('fonts/Inconsolata_Condensed-Black.ttf') format('truetype');
}

@font-face {
  font-family: 'Inconsolata-ExtraLight';
  src: url('fonts/Inconsolata_Condensed-ExtraLight.ttf') format('truetype');
}

@font-face {
  font-family: 'Inconsolata-Regular';
  src: url('fonts/Inconsolata_Condensed-Regular.ttf') format('truetype');
}

.comment {
  color: gray
}

.logo {
  position: fixed;
  left: 2em;
  top: 2em;
  color: white;
  font-size: 2vw;
  font-family: 'Inconsolata-Black';
}

section {
  display: block;
  height: 100%;
  width: 100%;
}

html {
  overflow: hidden;
}